.backdrop {
  z-index: 1000;
  opacity: 1;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, .2);
  padding: 20px 20px 38px;
  transition: opacity .5s linear, visibility .5s linear, transform .25s linear;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(0%)scale3d(1, 1, 1);
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: translateY(-100%);
}

